import React, { Component } from "react";
import { withRouter } from "react-router-dom";

import moment from "moment";
import {
  CSelect,
  CButton,
  CInput,
  CCard,
  CCardBody,
  CFormGroup,
  CCardHeader,
  CCol,
  CForm,
  CLabel,
  CRow,
  CDataTable,
} from "@coreui/react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const idLocale = require("moment/locale/id");
var dateNow = moment(Date.now()).locale("id", idLocale).format("LLLL");

class UserDetail extends Component {
  constructor() {
    super();
    this.state = {
      page: 1,
      totalPage: 1,
      stateModalSwab: false,
      cumanLihatProkes: false,
      clickedDateProkes: "",
      akunID: "",
      memberID: "",
      swabImage: "",
      statusList: [],
      diagnosaList: [],
      memberDetail: {},
      tampungGejala: [],
      listSwab: [],
      fullName: "",
      diagnosa: "",
      nik: "",
      gender: "",
      alamatDomisili: "",
      alamatIsolasiMandiri: "",
      mobilePhone: "",
      statusMonitoring: "",
      anamnesis: [],
      anamnesisSatuan: "",
      tglIsolasiStr: "",
      //Gejala
      isExist: false,
      demam: false,
      suhuDemam: "",
      demam14HariSebelumnya: false,
      batuk: false,
      kelelahan: false,
      sakitKepala: false,
      nyeriOtot: false,
      nyeriTenggorokan: false,
      pilek: false,
      tidakNafsuMakan: false,
      diare: false,
      anosmia: false,
      ageUsia: false,
      sesakNafas: false,
      nyeriDada: false,
      kulitKebiruan: false,
      sulitBangun: false,
      lainnyaGejala: "",
      //Prokes
      isoman: false,
      masker: false,
      berkumpul: false,
      cuciTangan: false,
      makan: false,
      kamar: false,
      pakaian: false,
      gejala: false,
      berjemur: false,
      mandi: false,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
  }
  componentDidMount() {
    this.setState({
      akunID: this.props.match.params.akunID,
      memberID: this.props.match.params.memberID,
    });
    this.getDetailPatient();
  }
  handleInputChange = (event) => {
    var k = event.target.id;
    this.setState({
      [k]: event.target.value,
    });
  };
  updateDataPatient = () => {
    var anamnesisTemp = this.state.anamnesis;
    if (this.state.anamnesisSatuan != "") {
      anamnesisTemp.push(this.state.anamnesisSatuan);
    }
    var body = {
      akunID: this.state.akunID,
      address: {
        alamatDomisili: this.state.alamatDomisili,
        alamatIsolasiMandiri: this.state.alamatIsolasiMandiri,
      },
      members: {
        memberID: this.state.memberID,
        fullName: this.state.fullName,
        nik: this.state.nik,
        birthDateStr: this.state.memberDetail.birthDateStr,
        age: this.state.memberDetail.age,
        gender: this.state.gender,
        job: this.state.memberDetail.job,
        mobilePhone: this.state.mobilePhone,
        anamnesis: anamnesisTemp,
        relationship: this.state.memberDetail.relationship,
        isIsolated: true,
        tglIsolasiStr:
          this.state.statusMonitoring == "Selesai dimonitor"
            ? ""
            : this.state.tglIsolasiStr != ""
            ? moment(this.state.tglIsolasiStr).format("yyyy-MM-DD HH:mm:ss")
            : "",
        statusMonitoring: this.state.statusMonitoring,
        diagnosa: this.state.diagnosa,
      },
    };
    console.log(body);
    fetch("https://api.aktiv.co.id/api/aktivengine/v1/patient/biodata/update", {
      method: "PUT",
      body: JSON.stringify(body),
      headers: new Headers({
        "Content-Type": "application/json",
        Authorization: localStorage.getItem("token"),
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.hasOwnProperty("status")) {
          if (data.status === "OK") {
            toast.success("Update Berhasil", {
              position: "top-right",
              autoClose: 1000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            this.getDetailPatient();
          } else {
            toast.error("Update Gagal " + data.statusmsg, {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }
        }
      })
      .catch((error) => console.error(error));
  };
  getDetailPatient = () => {
    fetch(
      `https://api.aktiv.co.id/api/aktivengine/v1/patient/get?id=${this.props.match.params.akunID}`,
      {
        method: "GET",
        headers: new Headers({
          "Content-Type": "application/json",
          Authorization: `${localStorage.getItem("token")}`,
        }),
      }
    )
      .then((response) => response.json())
      .then((data) => {
        var memberDetail;
        var patientDetail;
        patientDetail = data.data;
        data.data.members.forEach((element) => {
          if (element.memberID === this.props.match.params.memberID) {
            memberDetail = element;
          }
        });

        this.setState({
          patientDetail: patientDetail,
          memberDetail: memberDetail,
          fullName: memberDetail.fullName,
          diagnosa: memberDetail.diagnosa,
          nik: memberDetail.nik,
          gender: memberDetail.gender,
          alamatDomisili: patientDetail.address.alamatDomisili,
          alamatIsolasiMandiri: patientDetail.address.alamatIsolasiMandiri,
          mobilePhone: memberDetail.mobilePhone,
          statusMonitoring: memberDetail.statusMonitoring,
          tglIsolasiStr: moment(memberDetail.tglIsolasiStr).format(
            "yyyy-MM-DD"
          ),
          anamnesis:
            memberDetail.anamnesis != null ||
            memberDetail.anamnesis != undefined
              ? memberDetail.anamnesis
              : [],

          totalPage: data.data.total_page,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  };
  deleteMember = () => {
    if (window.confirm("Apakah anda yakin ingin menghapus data ini")) {
      var body = {
        akunID: this.state.akunID,
        members: {
          memberID: this.state.memberID,
        },
      };
      fetch(
        `https://api.aktiv.co.id/api/aktivengine/v1/patient/member/delete`,
        {
          method: "PUT",
          headers: new Headers({
            "Content-Type": "application/json",
            Authorization: `${localStorage.getItem("token")}`,
          }),
          body: JSON.stringify(body),
        }
      )
        .then((response) => response.json())
        .then((data) => {
          console.log("====== Delete Member Response =======");
          console.log(data);
          if (data.hasOwnProperty("status")) {
            if (data.status === "OK") {
              toast.success("Delete Berhasil", {
                position: "top-right",
                autoClose: 1000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
              this.props.history.goBack();
            } else {
              toast.error("Delete Gagal " + data.statusmsg, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  render() {
    return (
      <div className="container-fluid">
        <CRow>
          <CCol>
            <CCard>
              <CCardHeader>
                <strong> Biodata</strong>
                <CButton
                  className="float-right btn-facebook btn-brand"
                  onClick={() => {
                    this.updateDataPatient();
                  }}
                >
                  Simpan
                </CButton>
                <CButton
                  color="danger"
                  className="float-right mr-2"
                  onClick={() => {
                    this.deleteMember();
                  }}
                >
                  Hapus Member Ini
                </CButton>
              </CCardHeader>
              <CCardBody>
                <CForm
                  action=""
                  method="post"
                  encType="multipart/form-data"
                  className="form-horizontal"
                >
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Nama</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="fullName"
                          type="text"
                          onChange={this.handleInputChange}
                          value={this.state.fullName}
                        />
                      </p>
                    </CCol>
                  </CFormGroup>
                  {/* <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Diagnosa</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CSelect
                          id="diagnosa"
                          onChange={this.handleInputChange}
                          value={this.state.diagnosa}
                        >
                          <option value="" key="">
                            -- Pilih Diagnosa --
                          </option>
                          {this.state.diagnosaList.map((item) => {
                            return (
                              <option value={item.diagnosa} key={item.diagnosa}>
                                {item.diagnosa}
                              </option>
                            );
                          })}
                        </CSelect>
                      </p>
                    </CCol>
                  </CFormGroup> */}
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>NIK</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="nik"
                          type="text"
                          onChange={this.handleInputChange}
                          value={this.state.nik}
                        />
                      </p>
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Jenis Kelamin</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CSelect
                          id="gender"
                          onChange={this.handleInputChange}
                          value={this.state.gender}
                        >
                          <option value="" key="">
                            -- Pilih Jenis Kelamin --
                          </option>
                          <option value="L" key="L">
                            Laki-Laki
                          </option>
                          <option value="P" key="P">
                            Perempuan
                          </option>
                        </CSelect>
                      </p>
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Alamat Domisili</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="alamatDomisili"
                          type="text"
                          onChange={this.handleInputChange}
                          value={this.state.alamatDomisili}
                        />
                      </p>
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Alamat Isolasi</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="alamatIsolasiMandiri"
                          type="text"
                          onChange={this.handleInputChange}
                          value={this.state.alamatIsolasiMandiri}
                        />
                      </p>
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Handphone</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="mobilePhone"
                          type="text"
                          onChange={this.handleInputChange}
                          value={this.state.mobilePhone}
                        />
                      </p>
                    </CCol>
                  </CFormGroup>
                  {/* <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Tanggal Mulai Isolasi</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CInput
                          id="tglIsolasiStr"
                          type="date"
                          onChange={this.handleInputChange}
                          value={this.state.tglIsolasiStr}
                        />
                      </p>
                    </CCol>
                  </CFormGroup> */}
                  {/* <CFormGroup row>
                    <CCol md="3">
                      <CLabel>
                        <strong>Status</strong>
                      </CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <p className="form-control-static">
                        <CSelect
                          id="statusMonitoring"
                          onChange={this.handleInputChange}
                          value={this.state.statusMonitoring}
                        >
                          <option value="" key="">
                            -- Pilih Tahapan --
                          </option>
                          {this.state.statusList.map((item) => {
                            return (
                              <option value={item.status} key={item.status}>
                                {item.status}
                              </option>
                            );
                          })}
                        </CSelect>
                      </p>
                    </CCol>
                  </CFormGroup> */}
                </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
      </div>
    );
  }
}
export default withRouter(UserDetail);
